.cv_text {
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-bottom: 80px;
  }

.iconesAll{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;
    h4{
        border: 1px solid black;
        box-shadow: 0 0 42px rgba(0, 0, 0, 1.3);
        border-radius: 5px;
        padding: 10px; 
    }
}

.icone {
    height: 90px;
}
.iconesAllItem{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 10px;
    margin: 43px auto 50px;
}
.listIcones {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    gap: 100px;
    margin: 43px auto 50px;
}

.icone {
    margin-bottom: 20px;
}

.listIconesDetouring {
    border: 1px solid black;
    border-radius: 15px;
    padding: 5px;
    box-shadow: 0 0 22px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    padding: 10px; 
}

.ancres {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    text-align: center;
    padding-bottom: 50px;
    border-bottom: 1px dotted black;
}

