.cardsBackground {
  border-radius: 25px;
  display: grid;
  grid-gap: 80px;
  padding: 20px;
  width: 100%;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  a{
    display: flex;
    justify-content: center;
    align-items: center;
  }
}


.cards {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 400px;
    width: 200px;
    border: 3px solid black;
    border-radius: 30px;
    transition: transform 0.5s ease, width 0.3s ease, background-color 0.5s ease;
    
  
    &__text {
      display: flex;
      width: 100%;
      padding: 30px;
      border-radius: 20px 20px 0px 0px;
      background-color: black;
      color: white;
      text-align: center;
      justify-content: center;
      align-items: center;
      font-size: 13px;
      font-weight: 500;
 }
  
    &__card {
      width: 100%;
      height: 200px;
      object-fit: contain;
      margin: 0 auto;
      padding: 10px;
    }

    &__text__subtitle {
      display: flex;
      height: 130px;
      padding: 30px;
      border-radius: 0px 0px 20px 20px;
      background-color: black;
      color: white;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-size: 15px;
      font-weight: 500;
    }
  }

  .cards:hover {
    transform: scale(1.2);
    background-color: rgba(0, 0, 0, 0.284);
  }

  @media (min-width: 600px) {
    .cards {
      width: 300px; /* For screens wider than 600px, maintain the width of 300px */
    }
  }
  
  @media (min-width: 1200px) {
    .cards {
      width: 400px; /* For screens wider than 1200px, increase the width to 400px */
    }
  }
