@keyframes wave {
    0%, 100% {
      transform: rotate(0deg);
    }
    20%, 90% {
      transform: rotate(50deg);
    }
    40%, 80% {
      transform: rotate(-25deg);
    }
  }

  .smiley-hand {
    font-size: 4em;
    animation: wave 5s infinite;
    margin-bottom: 40px;
  }
  
